import React from "react"
import { createBrowserRouter, createHashRouter } from "react-router-dom"
import Login from "@/pages/User/Login"
import HomeIndex from "@/pages/Home"
import MainLayout from "@/pages/Layout/MainLayout/MainLayout"
import AuthLayout from "@/pages/Layout/AuthLayout/AuthLayout"
import UserList from "@/pages/User/UserList"
import NyanCatLayout from "@/pages/Layout/NyanCatLayout/NyanCatLayout"
// 
const router = createHashRouter([
  {
    path: "/",
    element: <MainLayout />,
    children: [
      {
        path: "/home",
        element: <HomeIndex />
      },
      {
        path: "/user/list",
        element: <UserList />
      }
    ]
  },
  // 
  {
    path: "/",
    element: <AuthLayout />,
    children: [
      {
        path: "/auth/login",
        element: <NyanCatLayout><Login /></NyanCatLayout>
      },
    ]
  }
])
export {
  router
}
