import { createSlice } from "@reduxjs/toolkit";
export const appSlice = createSlice({
  name: "app",
  initialState: {
    token: ""
  },
  reducers: {
    setToken: (state, action) => {
      state.token = action.payload
    },
    removeToken: (state) => {
      state.token = ""
    }
  }
})

const { actions, reducer } = appSlice
export const {
  setToken, removeToken
} = actions
export default reducer