import React from "react";
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import Collapse from '@mui/material/Collapse';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import { useNavigate } from 'react-router-dom';
interface menuItem {
  name: string,
  route: string,
  icon?: React.ReactNode | string,
  id: string | number,
  children?: menuItem[]
}
export default function Sidebar() {
  let mentList = [
    {
      id: "1",
      name: "",
      route: "/home",
      icon: "",
    },
    {
      id: "2",
      name: "",
      route: null,
      children: [
        {
          name: "",
          route: "/user/list",
          id: "3"
        }
      ]
    },
    {
      id: "4",
      name: "",
      route: "/auth/login",
      icon: "",
    },
  ] as Array<menuItem>
  const [selectedRoute, setSelectedRoute] = React.useState<menuItem>({ name: "", route: "", id: -1 });
  function onChangeSelect(routeItem: menuItem) {
    setSelectedRoute(routeItem)
  }
  return (<>
    <List>
      {mentList && mentList.map((item, index) => {
        return <Nav menu={item} onChangeSelect={onChangeSelect} selectedRoute={selectedRoute} key={"s" + item.id} />
      })}
    </List>
  </>)
}
function Nav(props: { menu: menuItem, selectedRoute: any, onChangeSelect: any }) {

  const [open, setOpen] = React.useState(false);
  const { menu, selectedRoute, onChangeSelect } = props
  // 
  function pushRoute(routeItem: menuItem) {
    navigate(routeItem.route)
  }
  const navigate = useNavigate()
  // ,
  if (menu.children && menu.children.length > 0) {
    return (
      <>
        <ListItem disablePadding key={"c" + menu.id}>
          <ListItemButton onClick={() => {
            onChangeSelect(menu)
            setOpen(!open)
          }}
            selected={selectedRoute.id === menu.id}
          >
            <ListItemIcon sx={{ minWidth: "42px" }}>
              <StarBorderIcon />
            </ListItemIcon>
            <ListItemText primary={menu.name} />
          </ListItemButton>
        </ListItem>
        <List>
          {menu.children.map((innerItem: any, innerIndex: number) => {
            return (<Collapse in={open} timeout="auto" key={"c" + menu.id + innerItem.id}>
              <List disablePadding sx={{ pl: 1 }}>
                <ListItem disablePadding onClick={() => {
                  onChangeSelect(innerItem)
                  pushRoute(innerItem)
                }}>
                  <ListItemButton selected={selectedRoute.id === innerItem.id}>
                    <ListItemIcon sx={{ minWidth: "42px" }}>
                      <StarBorderIcon />
                    </ListItemIcon>
                    <ListItemText primary={innerItem.name} />
                  </ListItemButton>
                </ListItem>
              </List>
            </Collapse>)
          })
          }
        </List>
      </>
    )
  } else {
    return (<>
      <ListItem disablePadding key={"c" + menu.id}>
        <ListItemButton onClick={() => {
          onChangeSelect(menu)
          pushRoute(menu)
        }}
          selected={selectedRoute.id === menu.id}
        >
          <ListItemIcon sx={{ minWidth: "42px" }}>
            <StarBorderIcon />
          </ListItemIcon>
          <ListItemText primary={menu.name} />
        </ListItemButton>
      </ListItem>
    </>)
  }
}