import React, { useEffect, useState } from "react";
import style from "./login.module.scss";
import { FormControl, TextField, Button } from "@mui/material";
import { useFormik } from 'formik';
import * as yup from 'yup'
import { useNavigate } from "react-router-dom";
import { useAppDispatch, useAppSelector } from "@/store/hooks";
import { RootState } from "@/store/store";
import { setToken, removeToken } from "@/store/features/appSlice";
interface FormValues {
  username: string,
  password: string
}
export default function Login() {
  const token = useAppSelector((state: RootState) => state.app.token)
  const dispatch = useAppDispatch()

  useEffect(() => {
    if (token) {
      dispatch(removeToken())
    }
  }, [token])
  const navigate = useNavigate()
  const formik = useFormik({
    initialValues: { username: '', password: '' } as FormValues,
    validationSchema: yup.object({
      username: yup.string().required(''),
      password: yup.string().required('')
    }),
    onSubmit: values => {
      // console.log(JSON.stringify(values, null, 2));
      // 
      handleSubmit(values)
    },
  })
  function handleSubmit(values: FormValues) {
    // token
    dispatch(setToken("123456"))
    navigate("/", {})
  }
  return (<div className={style.view}>
    <form onSubmit={formik.handleSubmit} className={style.formBox}>
      <FormControl>
        <TextField
          name="username"
          value={formik.values.username}
          onChange={formik.handleChange}
          helperText={formik.touched.username && formik.errors.username}
          label=""
          error={formik.touched.username && Boolean(formik.errors.username)}
          fullWidth
        />
      </FormControl>
      <FormControl>
        <TextField
          name="password"
          value={formik.values.password}
          onChange={formik.handleChange}
          error={formik.touched.password && Boolean(formik.errors.password)}
          helperText={formik.touched.password && formik.errors.password}
          label=""
          fullWidth
        />
      </FormControl>
      <FormControl>
        <Button variant="contained" type="submit"></Button>
      </FormControl>
    </form>
  </div>)
}