import React, { useEffect, useState } from "react";

import NyanCatGifBase64 from "./nyanCatGifBase64"
interface NyanCatLayoutProps {
  children: React.ReactNode
}
export default function NyanCatLayout(props: NyanCatLayoutProps) {
  const { children } = props
  return (<div style={{ position: "relative", width: "100%", height: "100%" }}>
    {children}
    <NyanCatList count={30} />
  </div>)
}
function NyanCatList(props: { count: number }) {
  return (<>
    {new Array(props.count).fill(null).map(_ => <NyanCat />)}
  </>)
}

function NyanCat() {

  // : 301119301/119
  const [width, setWidth] = useState(randomNum(100, 200))
  const [x, setX] = useState(randomNum(width, window.innerWidth - width))
  const [y, setY] = useState(randomNum(0, window.innerHeight - width * (119 / 301)))
  const [speed, setSpeed] = useState(randomNum(50, 200))
  const catStyle = {
    position: "absolute",
    left: x + "px",
    top: y + "px",
    width: width + "px",
    zIndex: -10000
  } as React.CSSProperties;
  useEffect(() => {
    let animationFrameId: any;

    // requestAnimationFramesetInterval
    function animate() {
      let nextX = x + speed / 60; // requestAnimationFrame60
      if (nextX >= window.innerWidth - width) {
        nextX = 0 - width;
      }
      setX(nextX);
      animationFrameId = requestAnimationFrame(animate); // 
    }

    animationFrameId = requestAnimationFrame(animate);

    // 
    return () => cancelAnimationFrame(animationFrameId);
  }, [x, width, speed]); // 

  return <img src={NyanCatGifBase64} style={catStyle} />
}
function randomNum(min: number, max: number) {
  return Math.random() * (max - min) + min;
}